.overlay {
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0%;
  z-index: 2;
  background-color: red;
}
.first {
  left: 0%;
}
.second {
  left: 33.3%;
}
.third {
  left: 66.6%;
}

/* Loading Screen */
.loading-screen {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: #0a0a0a;
  z-index: 2;
}

.ring {
  margin: 0%;
  padding: 0%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  z-index: 2;
  width: 40%;
  opacity: 0.7;
}

.ring img{
  width: 100%;
}

.ringOne img {
  animation: rotateFirstRing 2s linear infinite;
}

.ringTwo img {
  animation: rotateFirstRing 8s linear infinite;
}

@keyframes rotateFirstRing {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

